.playdate-grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
  grid-column-gap: 1rem;
}

.playdate-grid-container h3 {
  grid-row: 1;
  grid-column: 1 / span 2;
  color: #055c7d;
}

.playdate-grid-container h4 {
  color: #055c7d;
}

.requestor-info {
  grid-row: 2;
  grid-column: 1;
}

.requestor-dog-photo {
  grid-row: 3;
  grid-column: 2;
}

.reciever-info {
  grid-row: 2;
  grid-column: 2;
}

.recieving-dog-photo {
  grid-row: 2;
  grid-column: 2;
}

.playdate-content-container {
  margin-left: auto;
  margin-right: auto;
  padding: 1rem;
  width: 75%;
  grid-column: 1 / 3;
}

.playdate-details {
  grid-row: 4;
  grid-column: 1 / 3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: left;
}

.update-status {
  grid-row: 5;
  grid-column: 1 / 3;
  margin: 1rem;
}

.update-status .approve-btn {
  margin-right: 1rem;
}

